.btn-primary {
  background-color: #f9a826;
  border: 0;
  padding: 12px 35px;

  &:hover {
    background-color: #f79d0e;
  }
  &:focus {
    box-shadow: none;
    background-color: #f9a826;
    border: 0;
  }
}
.navbar {
  background-color: #fdcd3b;
  margin-bottom: 0px;
}

.jumbotron {
  border-radius: 0px;
  padding-bottom: 10vh;
}
